import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Login from '@/views/Login'
import Home from '@/views/Home'

import LiPeiList from '@/views/lipei/List.vue'
import LiPeiQuery from '@/views/lipei/Query.vue'
import XiaoGuanList from '@/views/xiaoguan/List.vue'
import BanGongShi from '@/views/bangongshi/List.vue'

import ZheCeXinXi from '@/views/kefu/zhucexinxi.vue'
import JiFenChanSheng from '@/views/kefu/jifenchansheng.vue'

import IpAddress from '@/views/xinxijishubu/ipaddress.vue'





Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
    },
    {
      path:'/lipei',
      component: Home,
      // name:'lipei',
      children: [
        { path: '/lipei/list', component: LiPeiList, name: 'LiPeiList' },
        { path: '/lipei/query', component: LiPeiQuery, name: 'LiPeiQuery' },
         ]
    },
    {
      path:'/xiaoguan',
      component: Home,
      // name:'lipei',
      children: [
        { path: '/xiaoguan/list', component: XiaoGuanList, name: 'XiaoGuanList' },
         ]
    },
    {
      path:'/bangongshi',
      component: Home,
      // name:'lipei',
      children: [
        { path: '/bangongshi/list', component: BanGongShi, name: 'BanGongShi' },
         ]
    },
    {
      path:'/kefu',
      component: Home,
      // name:'lipei',
      children: [
        { path: '/kefu/zhucexinxi', component: ZheCeXinXi, name: 'ZheCeXinXi' },
        { path: '/kefu/jifenchansheng', component: JiFenChanSheng, name: 'JiFenChanSheng' },
         ]
    },
    {
      path:'/xinxijishubu',
      component: Home,
      // name:'lipei',
      children: [
        { path: '/xinxijishubu/ipadress', component: IpAddress, name: 'IpAddress' },
         ]
    },
    {
      path:'/chexian',
      component: Home,
      // name:'lipei',
      children: [

         ]
    },
    {
      path:'/feichexian',
      component: Home,
      // name:'lipei',
      children: [

         ]
    },

  ]
})



