<template>
	<section>
		<!--工具条-->
        <a>（1）错误：该保单下存在未完成的接口表任务，不允许核赔通过，请通过以下查询业务单号</a>
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
         
		  <el-form :inline="true" :model="filters">

                <el-form-item label="保单号：">
                <el-input  v-model="filters.policyno"  placeholder="输入保单号"   style="width: 250px" ></el-input>
                </el-form-item>

                <el-form-item label="业务单号：">
                <el-input  v-model="filters.bussinsessno"   readonly="true" style="width: 250px"></el-input>
                </el-form-item>

                <el-form-item>
					<el-button type="primary" @click="getdata">查询</el-button>
				</el-form-item>

			</el-form>
		</el-col>
        





	</section>
</template>

<script>

	export default {
		data() {
			return {
				filters: {
                    policyno:'',
                    bussinsessno:''
				},
                listLoading:false, 
                 }
		    },
		methods: {
			//获取数据
			getdata:function() {
                if(this.checkpolicyno()){              
				let para = { 
                    policyno:this.filters.policyno, 
				};
                this.listLoading = true;
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/lipei/baoan',para,{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/lipei/yewudanhao',para,{ emulateJSON: true }).then((res) => {
                    console.log(res.data.data);
                    this.filters.bussinsessno = res.data.data['0']['businessno'];
					this.listLoading = false;
                    this.$message({
                        message: '查询成功！',
                         type: 'success'
                     });					
                   }).catch((err)=>{
                    this.$message({
                        message: '查询失败！',
                         type: 'error'
                     });
                     this.listLoading = false;
                   });
                 }
            },
            checkpolicyno(){
                if(this.filters.policyno.length!=22){
                    this.$alert('请输入22位保单号！');
                    return false
                }else{
                    return true
                }
            }
        },
	}

</script>

<style scoped>

</style>


