            checkdate(startdate,enddate){
                if(startdate=='' || enddate==''){
                        this.$message({
                        message: '请输入日期！',
                         type: 'error'
					 });
                    return false;
                } else{
                    return true;
                }   
			},
		openFullScreen() {
