<template>
	<section>
		<!-- 报案 -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
		  <el-form :inline="true" :model="baoan">
              <el-col :span="6">
                <el-form-item label="报案清单：（查询口径：报案日期）" >
                </el-form-item>
                </el-col>
                <el-form-item label="起始日期：" style="float:center" >
                <el-date-picker  v-model="baoan.startdate" type="datetime" placeholder="选择日期" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="终止日期：" style="float:center">
                <el-date-picker  v-model="baoan.enddate" type="datetime" placeholder="选择日期"  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item style="float:right" >
					<el-button  type="primary" @click="exportbaoan"  >导出Excel</el-button>
				</el-form-item>          
			</el-form>
		</el-col>
          <!-- 核损 -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
		  <el-form :inline="true" :model="hesun">
               <el-col :span="6">
              <el-form-item label="核损清单：（查询口径：最后定损日期）">
                </el-form-item>
               </el-col>
                <el-form-item label="起始日期：">
                <el-date-picker  v-model="hesun.startdate" type="datetime" placeholder="选择日期" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="终止日期：">
                <el-date-picker  v-model="hesun.enddate" type="datetime" placeholder="选择日期"  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item style="float:right">
					<el-button type="primary" @click="exporthesun"  >导出Excel</el-button>
				</el-form-item>          
			</el-form>
		</el-col>
          <!-- 送修 -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
		  <el-form :inline="true" :model="songxiu">
               <el-col :span="6">
              <el-form-item label="送修清单：（查询口径：核损日期）">
                </el-form-item>
               </el-col>
                <el-form-item label="起始日期：">
                <el-date-picker  v-model="songxiu.startdate" type="datetime" placeholder="选择日期" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="终止日期：">
                <el-date-picker  v-model="songxiu.enddate" type="datetime" placeholder="选择日期"  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item style="float:right">
					<el-button type="primary" @click="exportsongxiu"  >导出Excel</el-button>
				</el-form-item>          
			</el-form>
		</el-col>
          <!-- 结案 -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
		  <el-form :inline="true" :model="jiean">
               <el-col :span="6">
              <el-form-item label="结案清单：（查询口径：结案日期）">
                </el-form-item>
               </el-col>
                <el-form-item label="起始日期：">
                <el-date-picker  v-model="jiean.startdate" type="datetime" placeholder="选择日期" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="终止日期：">
                <el-date-picker  v-model="jiean.enddate" type="datetime" placeholder="选择日期"  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item style="float:right">
					<el-button type="primary" @click="exportjiean"  >导出Excel</el-button>
				</el-form-item>          
			</el-form>
		</el-col>		

	</section>
</template>

<script>
import { mapState } from 'vuex'
	export default {
		data() {	
			return {
				fullscreenLoading: false,
				page: 1, 
				loading:[],
				baoan:{startdate:'',enddate:''},
				hesun:{startdate:'',enddate:''},
				songxiu:{startdate:'',enddate:''},
				jiean:{startdate:'',enddate:''},
                 }
		    },
		methods: {
			//导出报案清单
			exportbaoan() {
                if(this.checkdate(this.baoan.startdate,this.baoan.enddate)){          
               this.openFullScreen();
				let para = { 
                    startdate:this.baoan.startdate,
                    enddate:this.baoan.enddate  
				};              
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/lipei/baoan',para,{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/lipei/baoan',para,{ emulateJSON: true }).then((res) => {	
					var url = "http://56.145.32.32/etl/data/报案清单.xls"
            		window.open(url);
                    this.$message({
                        message: '查询成功！',
                         type: 'success'
					 });		
					 	this.loading.close();				 				
                   }).catch((err)=>{
                    this.$message({
                        message: '查询失败！请控制日期范围在6个月内！',
                         type: 'error'
					 });
					 this.loading.close();
                   });
                 }
			},
			//导出核损清单
			exporthesun() {
                if(this.checkdate(this.hesun.startdate,this.hesun.enddate)){          
               	 this.openFullScreen();
				let para = { 
                    startdate:this.hesun.startdate,
                    enddate:this.hesun.enddate  
				};              
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/lipei/baoan',para,{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/lipei/hesun',para,{ emulateJSON: true }).then((res) => {	
					var url = "http://56.145.32.32/etl/data/核损清单.xls"
            		window.open(url);
                    this.$message({
                        message: '查询成功！',
                         type: 'success'
					 });	
					 this.loading.close();					 				
                   }).catch((err)=>{
                    this.$message({
                        message: '查询失败！请控制日期范围在6个月内！',
                         type: 'error'
					 });
					 this.loading.close();
                   });
                 }
			},
			//导出送修清单
			exportsongxiu() {
                if(this.checkdate(this.songxiu.startdate,this.songxiu.enddate)){          
               	 this.openFullScreen();
				let para = { 
                    startdate:this.songxiu.startdate,
                    enddate:this.songxiu.enddate  
				};              
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/lipei/baoan',para,{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/lipei/songxiu',para,{ emulateJSON: true }).then((res) => {	
					var url = "http://56.145.32.32/etl/data/送修清单.xls"
            		window.open(url);
                    this.$message({
                        message: '查询成功！',
                         type: 'success'
					 });	
					 this.loading.close();					 				
                   }).catch((err)=>{
                    this.$message({
                        message: '查询失败！请控制日期范围在6个月内！',
                         type: 'error'
					 });
					 this.loading.close();
                   });
                 }
			},
			exportjiean() {
                if(this.checkdate(this.jiean.startdate,this.jiean.enddate)){          
               	 this.openFullScreen();
				let para = { 
                    startdate:this.jiean.startdate,
                    enddate:this.jiean.enddate  
				};              
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/lipei/baoan',para,{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/lipei/jiean',para,{ emulateJSON: true }).then((res) => {	
					var url = "http://56.145.32.32/etl/data/结案清单.xls"
            		window.open(url);
                    this.$message({
                        message: '查询成功！',
                         type: 'success'
					 });	
					 this.loading.close();					 				
                   }).catch((err)=>{
                    this.$message({
                        message: '查询失败！请控制日期范围在6个月内！',
                         type: 'error'
					 });
					 this.loading.close();
                   });
                 }
			},
			
			






            checkdate(startdate,enddate){
                if(startdate=='' || enddate==''){
                        this.$message({
                        message: '请输入日期！',
                         type: 'error'
					 });
                    return false;
                } else{
                    return true;
                }   
			},
		openFullScreen() {
        this.loading = this.$loading({
          lock: true,
          text: '拼命加载中！不要刷新！',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
			}
		}
	}
</script>

