<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

		  <!-- <el-form :inline="true" :model="filters">
              <el-form-item label="客户注册信息：（查询口径：注册日期）">
                </el-form-item>
                <el-form-item label="起始日期：">
                <el-date-picker  v-model="filters.startdate" type="datetime" placeholder="选择日期" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="终止日期：">
                <el-date-picker  v-model="filters.enddate" type="datetime" placeholder="选择日期"  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item>
					<el-button type="primary" @click="getdata">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"    @click="exportExcel"  >导出Excel</el-button>
				</el-form-item>
               


			</el-form> -->
		</el-col>
        
		<!--列表-->
		<el-table :data="data"    v-loading="listLoading"  style="width: 100% height:100%"   id="table" >
		
            <el-table-column prop="ip" label="IP地址" width="215"  ></el-table-column>

            <el-table-column prop="name" label="姓名" width="215" ></el-table-column>

            <el-table-column prop="dep" label="部门" width="215" ></el-table-column>


             <el-table-column prop="remark" label="备注" mid-width="215" ></el-table-column>

            <el-table-column label="操作" mid-width="215" >
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
				</template>
			</el-table-column>


    <!-- 编辑界面 -->
		</el-table>
            <el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px"  ref="editForm">

				<el-form-item label="IP地址" prop="ip">
					<el-input v-model="editForm.ip" auto-complete="off" readonly="readonly"></el-input>
				</el-form-item>

				<el-form-item label="姓名" prop="name">
					<el-input v-model="editForm.name" auto-complete="off" ></el-input>
				</el-form-item>

                <el-form-item label="部门" prop="dep">
					<el-input v-model="editForm.dep" auto-complete="off" ></el-input>
				</el-form-item>

				<el-form-item label="备注" prop="remark">
					<el-input v-model="editForm.remark" auto-complete="off" ></el-input>
				</el-form-item>

				
				</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>



	</section>
</template>

<script>

import FileSaver from 'file-saver'
import XLSX from 'xlsx'
import moment from "moment"
	export default {
		data() {
			return {

                //编辑界面数据
				editForm: {
                    ip: '',
                    name:'',
                    dep:'',
                    remark:''
                },
                sels: [],//列表选中列
				editFormVisible: false,//编辑界面是否显示
                data:[],
                listLoading:false, 
                editLoading: false,
                 }
		    },
		methods: {
	//     exportExcel(){
    //         var url = "http://56.145.32.32/etl/data/反洗钱清单.xls"
    //         window.open(url);
    // },
            // 导出excle
            
		 exportExcel () {
         /* generate workbook object from table */
		 var wb = XLSX.utils.table_to_book(document.getElementById('table'))
         /* get binary string as output */
         var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
         try {
             FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '商城客户注册信息.xlsx')
         } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
         return wbout
       },
               	//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//获取数据
			getdata:function() {
                          
                this.listLoading = true;
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/xinxijishubu/getipaddress',{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/xinxijishubu/getipaddress',{ emulateJSON: true }).then((res) => {
                    this.data = res.data.data;
					this.listLoading = false;
                    this.$message({
                        message: '查询成功！',
                         type: 'success'
                     });					
                   }).catch((err)=>{
                    this.$message({
                        message: '查询失败！',
                         type: 'error'
                     });
                     this.listLoading = false;
                   });
                 
            },
            			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							// this.$http.post('http://56.145.32.63:8080/xinxijishubu/updateipaddress',para,{ emulateJSON: true }).then((res) => {
							this.$http.post('http://56.145.32.32:8082/xinxijishubu/updateipaddress',para,{ emulateJSON: true }).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.code = res.data.code;
								if(this.code==1){
								this.$message({
									message: '修改成功！',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getdata();
								}else{
									this.$message({
									message: '修改失败！',
									type: 'error'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getdata();
								}

							});
						});
					}
				});
			},


        },

         mounted() {
			this.getdata();
		}

	}

</script>

<style scoped>

</style>


