<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

		  <el-form :inline="true" :model="filters">
              <el-form-item label="积分产生清单：（查询口径：签单日期）">
                </el-form-item>
                <el-form-item label="起始日期：">
                <el-date-picker  v-model="filters.startdate" type="datetime" placeholder="选择日期" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="终止日期：">
                <el-date-picker  v-model="filters.enddate" type="datetime" placeholder="选择日期"  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item>
					<el-button type="primary" @click="getdata">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"    @click="exportExcel"  >导出Excel</el-button>
				</el-form-item>
               


			</el-form>
		</el-col>
        
		<!--列表-->
		<el-table :data="data"    v-loading="listLoading"  style="width: 100%"  height="700" id="table" >
             <el-table-column prop="jifen" label="积分数量" width="215"  sortable></el-table-column>
             <el-table-column prop="operatedate" label="签单日期" width="215"  :formatter="dateFormat" sortable></el-table-column>
            <el-table-column prop="comname" label="支公司名称" width="215" ></el-table-column>
            <el-table-column prop="comcodename" label="网点名称" width="215" ></el-table-column>
            <el-table-column prop="policyno" label="保单号" width="215" ></el-table-column>
            <el-table-column prop="sumpremium" label="应交保费" width="215" ></el-table-column>
            <el-table-column prop="sumtaxpremium" label="增值税" width="215" ></el-table-column>
            <el-table-column prop="sumnetpremium" label="纯保费" width="215" ></el-table-column>
            <el-table-column prop="agentname" label="渠道名称" width="215" ></el-table-column>
            <el-table-column prop="clausetype" label="条款产品" width="215" ></el-table-column>
            <el-table-column prop="carkindname" label="车辆种类名称" width="215" ></el-table-column>
            <el-table-column prop="startdate" label="起保日期" width="215" ></el-table-column>
            <el-table-column prop="enddate" label="终保日期" width="215" ></el-table-column>
            <el-table-column prop="licenseno" label="号牌号码" width="215" ></el-table-column>
            <el-table-column prop="businessnaturename" label="业务来源" width="215" ></el-table-column>
            <el-table-column prop="riskcode" label="险种代码" width="215" ></el-table-column>
            <el-table-column prop="monopolycode" label="送修码" width="215" ></el-table-column>
            <el-table-column prop="monopolyname" label="送修码名称" width="215" ></el-table-column>
            <el-table-column prop="handler1name" label="归属人员名称" width="215" ></el-table-column>
            <el-table-column prop="handlername" label="经办人员名称" width="215" ></el-table-column>
            <el-table-column prop="policynotime" label="转保单日期" width="215" ></el-table-column>
            <el-table-column prop="projectcode" label="项目代码" width="215" ></el-table-column>
            <el-table-column prop="insuredname" label="投保人名称" width="215" ></el-table-column>
            <el-table-column prop="bbrinsuredname" label="被保险人名称" width="215" ></el-table-column>
            <el-table-column prop="xinxuzhuan" label="新续传标志" width="215" ></el-table-column>
            <el-table-column prop="mainsellfeerate" label="主渠道跟单比例" width="215" ></el-table-column>
            <el-table-column prop="sellfeerate" label="副渠道跟单比例" width="215" ></el-table-column>
            <el-table-column prop="summainsellfeerate" label="主渠道总比例" width="215" ></el-table-column>
            <el-table-column prop="sumsellfeerate" label="副渠道总比例" width="215" ></el-table-column>
            <el-table-column prop="maxcostrate" label="费用上限" width="215" ></el-table-column>
            <el-table-column prop="lastpolicyno" label="上年保单" width="215" ></el-table-column>
            <el-table-column prop="usenaturename" label="使用性质" width="215" ></el-table-column>
            <el-table-column prop="insuredtype" label="客户类型" width="215" ></el-table-column>
            <el-table-column prop="clausecode" label="特约代码" width="215" ></el-table-column>
            <el-table-column prop="clausename" label="特约名称" width="215" ></el-table-column>
            <el-table-column prop="clauses" label="特约内容" width="215" ></el-table-column>
		</el-table>




	</section>
</template>

<script>

import FileSaver from 'file-saver'
import XLSX from 'xlsx'
import moment from "moment"
	export default {
		data() {
			return {
				filters: {
                    startdate:'',
                    enddate: '',
				},
                data:[],
                listLoading:false, 
                 }
		    },
		methods: {
	//     exportExcel(){
    //         var url = "http://56.145.32.32/etl/data/反洗钱清单.xls"
    //         window.open(url);
    // },
    		// 导出excle
		 exportExcel () {
         /* generate workbook object from table */
		 var wb = XLSX.utils.table_to_book(document.getElementById('table'))
         /* get binary string as output */
         var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
         try {
             FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '积分产生清单.xlsx')
         } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
         return wbout
       },
			//获取数据
			getdata:function() {
                if(this.checkdate()){              
				let para = { 
                    startdate:this.filters.startdate,
                    enddate:this.filters.enddate  
				};
                this.listLoading = true;
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/lipei/baoan',para,{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/kefu/jifenchansheng',para,{ emulateJSON: true }).then((res) => {
                    this.data = res.data.data;
					this.listLoading = false;
                    this.$message({
                        message: '查询成功！',
                         type: 'success'
                     });					
                   }).catch((err)=>{
                    this.$message({
                        message: '查询失败！',
                         type: 'error'
                     });
                     this.listLoading = false;
                   });
                 }
            },
         dateFormat:function(row, column) {  
               var date = row[column.property];  
          if (date == undefined) {  
             return "";  
          }  
          return moment(date).format("YYYY-MM-DD HH:mm:ss");  
        },
            checkdate(){
                if(this.filters.startdate=='' || this.filters.enddate==''){
                    this.$alert('请输入日期！');
                    return false;
                } else{
                    return true;
                }   
            },

        },
	}

</script>

<style scoped>

</style>


